<template>
    <div>
        <user-search></user-search>
        <user-table :columnsList="tableColumns"></user-table>
        <user-form></user-form>
    </div>
</template>

<script>
    import userForm from '../common/form/user-form.vue';
    import userTable from '../common/table/table-list.vue';
    import userSearch from '../common/search/input-search.vue';
    import userlist_th from './userlist_th.js';

    export default {
        components: {
            userForm,
            userTable,
            userSearch
        },
        data () {
            return {
                tableColumns: []
            }
        },
        created: function() {
            this.tableColumns = userlist_th;
        }
    }
</script>


