export const userlist_th = [
    {
        type: 'index',
        title: '#',
        key: 'index',
        align: 'center'
    },
    {
        title: 'User Name',
        key: 'UserName'
    },
    {
        title: 'Power',
        key: 'Power'
    },
    {
        title: 'Admin',
        key: 'management'
    },
    {
        title: 'Email',
        key: 'email'
    },
    {
        title: 'Action',
        key: 'handle',
        width: '200px',
        align: 'center',
        handle: ['edit', 'delete']
    }
];

export default userlist_th;


